
.flexd-conmponent {
	width: 100%;
}
.nav-component {
	background: rgb(255, 255, 255);
	position: relative;
}
.nav-component:after {
	position: absolute;
	bottom: 0;
	width: 100%;
	content: '';
	border-bottom: 1px solid #eeeeee;
}
@media screen and (-webkit-min-device-pixel-ratio: 2) {
.nav-component:after {
		border-bottom: 0.5px solid #eeeeee;
}
}
@media screen and (-webkit-min-device-pixel-ratio: 3) {
.nav-component:after {
		border-bottom: 0.3px solid #eeeeee;
}
}
.nav-component ul li a.router-link-active {
	font-weight: 700;
	color: #3190e8;
	position: relative;
}
.nav-component ul li a.router-link-active:after {
	position: absolute;
	bottom: -0.16rem;
	left: 0;
	background-color: #2395ff;
	height: 0.06rem;
	content: '';
	width: 100%;
}
.nav-component ul {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-direction: row;
	        flex-direction: row;
}
.nav-component ul li {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	font-size: 0.28rem;
	color: rgb(77, 85, 93);
	text-align: center;
	height: 0.82rem;
	line-height: 0.82rem;
}
.nav-component ul li a {
}
.nav-cont-component {
}
@import './common/style/reset.css'



